.scroll-top {
  z-index: 1;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  transition: opacity .2s, transform .2s, visibility .2s;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 10px;
  transform: translateY(20px);
}

