  transition: background-color .2s;
  position: absolute;
}

.scroll-top:before {
  transform-origin: 0 0;
  transform: translate(50%)rotate(45deg);
}

.scroll-top:after {
  transform-origin: 100% 0;
  transform: translate(-50%)rotate(-45deg);
}

.scroll-top.visible {
  opacity: 1;
